import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import CSS ca AOS
AOS.init({
  duration: 1000, // Thi gian animation mc nh (tnh bng ms)
  once: true,    // Ch chy animation mt ln khi cun n
  mirror: false, // Khng lp li animation khi cun ln/xung
});
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
