import React, { useState } from "react";
import "./../../components/css/ChatBot.css";

const ChatBot = ({ isOpen, onClose }) => {
  const [messages, setMessages] = useState([
    { from: "bot", text: "Cho bn! Mnh c th h tr g v BTEC?" },
  ]);
  const [input, setInput] = useState("");

  const handleSend = () => {
    if (!input.trim()) return;
    const userMessage = { from: "user", text: input };
    const botMessage = { from: "bot", text: getBotReply(input) };
    setMessages([...messages, userMessage, botMessage]);
    setInput("");
  };

  const getBotReply = (text) => {
    const msg = text.toLowerCase();
    if (msg.includes("ngnh")) return "Trng c CNTT, Thit k  ha, QTKD...";
    if (msg.includes("hc ph")) return "Hc ph trung bnh 2530 triu/k.";
    if (msg.includes("tuyn sinh")) return "ng k ti: https://btec.fpt.edu.vn/dang-ky";
    if (msg.includes("lin h")) return "Hotline: 1900 8080  Fanpage: fb.com/btecvietnam";
    return "Bn c th hi v ngnh hc, hc ph, tuyn sinh, lin h...";
  };

  if (!isOpen) return null;

  return (
    <div className="chatbot-container">
      <div className="chatbot-header">
         H tr BTEC
        <button onClick={onClose} className="close-btn"></button>
      </div>
      <div className="chatbot-body">
        {messages.map((msg, i) => (
          <div key={i} className={`chat-message ${msg.from}`}>{msg.text}</div>
        ))}
      </div>
      <div className="chatbot-input">
        <input
          type="text"
          placeholder="Nhp cu hi..."
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && handleSend()}
        />
        <button onClick={handleSend}>Gi</button>
      </div>
    </div>
  );
};

export default ChatBot;
