// src/App.js
import React, { useState } from 'react';
import './index.css';

// Import components
import MouseTrail from './components/js/MouseTrail';
import Header from './components/js/Header';
import HeroSection from './components/js/HeroSection';
import IntroBTEC from './components/js/IntroBTEC';
import MidSectionImageText from './components/js/MidSectionImageText';
import SummerBTEC from './components/js/SummerBTEC';
import SmallCardsSection from './components/js/SmallCardsSection';
import InspirationGallery from './components/js/InspirationGallery';
import ContactSection from './components/js/ContactSection';
import Footer from './components/js/Footer';
import ChatBot from './components/js/ChatBot'; // Thm dng ny

// Import assets
import summerVibesImg from './assets/mid-section-img-1.jpg';
import ancientWisdomImg from './assets/mid-section-img-2.jpg';

function App() {
  const [isChatOpen, setIsChatOpen] = useState(false);

  const handleChatToggle = () => {
    setIsChatOpen(true);
  };

  return (
    <div className="App">
      <MouseTrail />
      <Header onChatToggle={handleChatToggle} /> {/* Truyn props vo Header */}
      <main>
        <HeroSection />
        <IntroBTEC />
        <MidSectionImageText
          image={summerVibesImg}
          title="Cho H Rc R Cng BTEC!"
          description="n cho mt ma h y nng lng v khm ph ti Cao ng quc t BTEC. Ni y, mi s t s tm thy con ng ring  pht trin."
          buttonText="Xem L Trnh Hc"
          buttonHref="https://btec.fpt.edu.vn/"
          isReversed={false}
        />
        <SmallCardsSection />
        <SummerBTEC />
        <MidSectionImageText
          image={ancientWisdomImg}
          title="Gi Tr C Truyn  Nn Tng Vng Chc"
          description="BTEC t ho gn gi v pht huy nhng nt p c truyn Vit Nam, lng ghp vo chng trnh hc  to nn nhng c nhn ton din."
          buttonText="Khm ph di sn"
          buttonHref="#intro"
          isReversed={true}
        />
        <InspirationGallery />
        <ContactSection />
      </main>
      <Footer />
      <ChatBot isOpen={isChatOpen} onClose={() => setIsChatOpen(false)} /> {/* Chatbot hin th cui trang */}
    </div>
  );
}

export default App;
