import React, { useState, useEffect } from 'react';
import '../css/Header.css';
import BTECLogo from './../../assets/btec-logo.png.png'; // Gi nh y l logo mc nh (v d: mu)
import BTECLogoWhite from './../../assets/btec-logo.png.png'; // Gi nh y l logo mu trng (nu c file ny)
// Nu bn ch c 1 file logo v n t i mu qua CSS, ch cn import 1 ci

function Header({ onChatToggle }) {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <div className="container">
        <div className="logo-section">
          {/* Bc logo trong th <a>  n c th click c */}
          {/* Dn v u trang (c th dng "#top" nu bn c id="top"  <body> hoc <div> ngoi cng) */}
          {/* Hoc v "/" nu y l ng dng Router v bn mun v trang ch */}
          <a href="https://btec.fpt.edu.vn/"> {/* Link v u trang */}
            <img
              src={isScrolled ? BTECLogoWhite : BTECLogo} // Logo i mu khi scroll
              alt="BTEC Logo"
              className="btec-logo"
            />
          </a>
        </div>
        <nav className="navbar">
          <ul>
            <li><a href="https://btec.fpt.edu.vn/">Trang Ch</a></li> {/* Vn l #home hoc "/" */}
            <li><a href="#intro-split">Gii Thiu</a></li> {/* CHNH SA: id ca section IntroBTEC */}
            <li><a href="#summer">Ma H BTEC</a></li> {/* CHNH SA: id mi cho section Ma H */}
            <li><a href="#contact">Lin H</a></li>
          </ul>
        </nav>

        {/* Nt Nhn T Vn gi chatbot */}
        <button className="get-quote-btn" onClick={onChatToggle}>
          Nhn T Vn
        </button>
      </div>
    </header>
  );
}

export default Header;