// src/components/js/SmallCardsSection.js
import React from 'react';
import './../../components/css/SmallCardsSection.css';
import iconSummer from './../../assets/small-card-icon-1.jpg'; // V d icon
import iconStudent from './../../assets/small-card-icon-2.jpg';
import iconTradition from './../../assets/small-card-icon-3.jpg';

function SmallCardsSection() {
  return (
    <section className="small-cards-section">
      <div className="container">
        <h2 className="section-title">Tinh Hoa BTEC  Nhng Gi Tr Vt Thi Gian</h2>
        <div className="small-cards-grid">
          <div className="card-box small-card-item">
            <img src={iconSummer} alt="Icon Ma H" />
            <h3>Nng Lng Ma H</h3>
            <p>Khng kh hc tp si ni, sng to, trn y sc sng ca <span className="keyword">ma h</span>.</p>
          </div>
          <div className="card-box small-card-item">
            <img src={iconStudent} alt="Icon S T" />
            <h3>Tm Huyt S T</h3>
            <p>Tinh thn kin tr, vt kh ca mi <span className="keyword">s t</span> trn con ng tri thc.</p>
          </div>
          <div className="card-box small-card-item">
            <img src={iconTradition} alt="Icon C Truyn" />
            <h3>Bn Sc C Truyn</h3>
            <p>Lng ghp v pht huy nhng gi tr <span className="keyword">c truyn</span> vn ha Vit.</p>
          </div>
        </div>
      </div>
    </section>
  );
}
export default SmallCardsSection;