// src/components/js/ContactSection.js

import React, { useState } from 'react'; // Import useState  qun l form
import './../../components/css/ContactSection.css';
import Stepper, { Step } from './Stepper'; // Import Stepper v Step components ca bn

function ContactSection() {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    phone: '',
    major: '',
    message: ''
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFinalStepCompleted = () => {
    console.log("All steps completed! Form Data:", formData);
    alert('Cm n bn  ng k t vn! Chng ti s lin h li sm.');
    //  y, bn c th gi d liu formData ln server
    // V d: fetch('/api/submit-form', { method: 'POST', body: JSON.stringify(formData) });
  };

  return (
    <section id="contact" className="contact-section">
      <div className="container">
        <h2 className="section-title">Kt Ni Cng BTEC  Chp Cnh c M</h2>
        <p className="contact-description">
          Bn mun tm hiu thm v <span className="keyword">Cao ng quc t BTEC </span>
          hay hnh trnh hc tp dnh cho <span className="keyword">s t</span> trong <span className="keyword">ma h</span> ny?
          Hy ng k t vn  chng ti lin h v h tr bn.
        </p>

        {/* --- TCH HP STEPPER VO Y --- */}
        <div className="contact-stepper-wrapper card-box"> {/* Wrapper  Stepper c khung nu cn */}
          <Stepper
            initialStep={1}
            onStepChange={(step) => {
              console.log(`Current step: ${step}`);
            }}
            onFinalStepCompleted={handleFinalStepCompleted}
            backButtonText="Quay li"
            nextButtonText="Tip tc"
          >
            {/* Bc 1: Thng tin lin h c bn */}
            <Step>
              <h3>Thng Tin C Bn</h3>
              <p>Vui lng in thng tin  chng ti c th lin h vi bn.</p>
              <div className="form-group">
                <label htmlFor="fullName">H v tn:</label>
                <input
                  type="text"
                  id="fullName"
                  name="fullName"
                  value={formData.fullName}
                  onChange={handleInputChange}
                  placeholder="Nguyn Vn A"
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="email">Email:</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  placeholder="email@example.com"
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="phone">S in thoi:</label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={formData.phone}
                  onChange={handleInputChange}
                  placeholder="09xx.xxx.xxx"
                />
              </div>
            </Step>

            {/* Bc 2: Ngnh hc quan tm */}
            <Step>
              <h3>Ngnh Hc Quan Tm</h3>
              <p>Bn quan tm n ngnh no ti BTEC?</p>
              <div className="form-group">
                <label htmlFor="major">Chn ngnh:</label>
                <select
                  id="major"
                  name="major"
                  value={formData.major}
                  onChange={handleInputChange}
                  required
                >
                  <option value="">-- Chn ngnh hc --</option>
                  <option value="thiet-ke-do-hoa">Thit K  Ha</option>
                  <option value="lap-trinh-may-tinh">Lp Trnh My Tnh</option>
                  <option value="quan-tri-kinh-doanh">Qun Tr Kinh Doanh</option>
                  <option value="marketing">Bn Dn</option>
                  <option value="khac">Khc</option>
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="message">Li nhn cho BTEC (Ty chn):</label>
                <textarea
                  id="message"
                  name="message"
                  value={formData.message}
                  onChange={handleInputChange}
                  rows="4"
                  placeholder="V d: Em mun tm hiu v hc bng..."
                ></textarea>
              </div>
            </Step>

            {/* Bc 3: Xc nhn */}
            <Step>
              <h3>Xc Nhn Thng Tin</h3>
              <p>Vui lng kim tra li thng tin trc khi gi:</p>
              <div className="summary-info">
                <p><strong>H v tn:</strong> {formData.fullName || 'Cha in'}</p>
                <p><strong>Email:</strong> {formData.email || 'Cha in'}</p>
                <p><strong>S in thoi:</strong> {formData.phone || 'Cha in'}</p>
                <p><strong>Ngnh quan tm:</strong> {formData.major || 'Cha chn'}</p>
                <p><strong>Li nhn:</strong> {formData.message || 'Khng c'}</p>
              </div>
              <p className="final-message">Bn  sn sng gi ng k t vn?</p>
            </Step>
          </Stepper>
        </div>
        {/* --- KT THC STEPPER --- */}
        <p className="contact-note">
          Chng ti cam kt bo mt thng tin c nhn ca bn. Mi thng tin s c s dng ch  lin h v t vn.
        </p>
      </div>
    </section>
  );
}

export default ContactSection;